@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100; 0,200; 0,300; 0,400; 0,500; 0,600; 0,700; 0,800; 0,900; 1,100; 1,200; 1,300; 1,400; 1,500; 1,600; 1,700; 1,800; 1,900&display=swap'); :root { --mc:#2334E3; } 
body { background-color: black; } 
body { color: white; } 

.container { width: 1420px; margin: 0 auto; } 
h2 { font-size: 40px; font-family: 'Poppins', sans-serif; font-weight: 600; } 
p { font-size: 15px; font-family: 'SUIT'; line-height: 1.45; } 




.Header .container { width: 1420px; margin: 0 auto; display: flex; justify-content: space-between; line-height: 100px; } 
.Header a { display: block; } 
.Header .Gnb>ul { display: flex; gap: 40px; font-size: 18px; font-family: 'Poppins', sans-serif; } 
.Header .icon { display: flex; gap: 20px; } 


.main_visual { position: relative; background: url(../public/assets/img/mv.png) no-repeat center center/cover; height: calc(100vh - 100px); } 

.main_visual .slogan { position: absolute; inset: 75% auto auto 50%; transform: translate(-50%,-50%); width: 1420px; font-family: 'Poppins', sans-serif; } 
.main_visual .slogan p { font-size: 150px; font-weight: 800; } 
.main_visual .slogan strong { font-size: 250px; font-weight: 600; letter-spacing: -0.015em; } 

.main_visual .main_object { position: absolute; inset: 10% auto auto 50%; transform: translateX(50%); } 
.main_visual .main_object>img { width:500px; animation: motion 2.5s linear 0s infinite alternate; } 
@keyframes motion { 
 0% { margin-top: 0px; } 
100% { margin-top: 30px; } 
 }

.story_of_apollo { padding: 30px 0 100px 0; } 
.story_of_apollo .container h2 { font-size: 240px; color:var(--mc); font-family: 'Poppins', sans-serif; font-weight: 600; text-align: right; } 

.eagle { padding: 100px 0; } 
.eagle h2 { margin: 0 0 20px 0; } 
.eagle p { margin: 0 0 50px 0; } 

.moon { width: calc(100% - 720px - ((100% - 1420px) / 2)); float: right; } 
.moon img { margin: 0 0 50px 0; } 

.motion { clear: both; padding-bottom: 100px; } 
.motion .container { position: relative; } 
.motion img { width: 800px; } 
.motion .path { position: absolute; left: 50px; bottom: 50px; width: 800px; } 
.motion .ball { position: absolute; left: 48px; top: -52px; width: 20px; height: 20px; background-color: white; border-radius: 50%; offset: path('M582.997 121.297C681.51 195.613 750.299 289.86 781.908 382.119C813.52 474.384 807.92 564.546 757.874 630.888C707.828 697.229 622.67 727.374 525.271 722.317C427.88 717.261 318.365 677.001 219.851 602.685C121.337 528.369 52.5488 434.122 20.9396 341.863C-10.6718 249.598 -5.07269 159.436 44.9735 93.0944C95.0197 26.753 180.177 -3.39241 277.576 1.66448C374.968 6.72105 484.483 46.9811 582.997 121.297Z') auto; animation: moveto 10s linear 0s infinite; } 
@keyframes moveto { 
 0% { offset-distance: 0% } 
100% { offset-distance: 100%; } 
 }


.earth { padding: 100px 0 500px 0; background: url(../public/assets/img/section_04.png) no-repeat center center/cover; } 
.earth p { margin: 0 0 50px 0; } 
.earth a { padding: 10px 30px; border-radius: 25px; border: 1px solid white; font-size: 15px; font-family: 'SUIT'; } 


.slide { padding: 200px 0; } 
.slide h2 { text-align: center; margin: 0 0 50px 0; } 
.slide .top { margin: 0 0 20px 0; } 

.slide02 { height: 100vh; } 

.mySwiper3 .storyslide {position: relative; }
.mySwiper3 .storyslide .inner{position: absolute; top: 50%; left:calc((100vw - 1420px) / 2); transform: translateY(-50%);}
.mySwiper3 .storyslide .inner p{margin: 0 0 20px 0;}
.mySwiper3 .storyslide .inner h2{line-height: 1.3; margin: 0 0 50px 0;}
.mySwiper3 .storyslide .inner a{ padding: 10px 30px; border-radius: 25px; border: 1px solid white; font-size: 15px; font-family: 'SUIT'; } 